import React from "react";
import EditorTitle from "./EditorTitle";
import { connect } from "react-redux";
import { setInscription, setFont } from "./../../../redux/editor-reducer";

const EditorTitleContainer = (props) => {
  return (
    <EditorTitle {...props}/>
  );
};

const mapStateToProps = (state) => ({
    title: state.editor.inscription,
    titleColor: state.editor.titleColor,
    font: state.editor.font,
    initialFont: state.editor.fonts[0],
    activeIcons: state.editor.activeIcons,
    isBacklightEnable: state.editor.isBacklightEnable
})

export default connect(mapStateToProps, { setInscription, setFont })(EditorTitleContainer);
