import React, { useEffect, useState } from "react";
import ToggleSwitch from "../../Common/ToggleSwitch/ToggleSwitch";
import classes from "./editorBacklightComponent.module.sass";

const EditorBacklightComponent = (props) => {
  const [isActive, setIsActive] = useState(false);

  useEffect(() => {
    props.setBacklightState(isActive);
  }, [isActive]);

  useEffect(() => {
    setIsActive(props.isBacklightEnable);
  }, []);

  return (
    <div className={classes.wrapper}>
      <ToggleSwitch
        isActive={props.isBacklightEnable}
        setIsActive={setIsActive}
      />
      {isActive ? (
        <div>{"   c "}</div>
      ) : (
        <div>{"    "}</div>
      )}
    </div>
  );
};

export default EditorBacklightComponent;
