import { ColorPicker } from "react-iro";
import iro from "@jaames/iro";
import React from "react";
import classes from "./colorPicker.module.sass"

const ColorPickerSlider = (props) => {
  return (
    <div className={classes.wrapper}>
      <ColorPicker
        setters={{
          onChangeColor: (color) => {
            props.setTitleColor(color.hexString);
          },
        }}
        options={{
          color: props.color === "#fff" ? '#76eaf1' : props.color,
          layout: [
            {
              component: iro.ui.Slider,
              options: {
                sliderType: "hue",
                width: 516,
                height: 15,
                borderWidth: 1,
                borderColor: "#fff",
                wheelLightness: false,
                activeHandleRadius: 7,
                handleRadius: 5,
              },
            },
          ],
        }}
      />
      <div onClick={() => props.setTitleColor('#fff')} className={classes.white_button}></div>
    </div>
  );
};

export default ColorPickerSlider;
