import React from "react";
import { Formik, Form, Field } from "formik";
import classes from "./../../EditorTitleComponent/editorTitleComponent.module.sass";
import onFieldChange from "../../../Common/FieldChangeHandler/FieldChangeHandler";

const EditorIconsMenu = (props) => {
  let fieldHandler = onFieldChange(props.setActiveIconSide);

  const deleteIcon = (icon, side) => {
    props.setActiveIcon(icon, side);
  };

  return (
    <>
      <Formik
        initialValues={{
          menu: "left",
        }}
      >
        {({ values }) => (
          <Form>
            <div
              className={classes.wrapper}
              role="group"
              aria-labelledby="radio-group"
            >
              <label>
                <Field
                  type="radio"
                  name="menu"
                  value="left"
                  onClick={fieldHandler(values.menu)}
                />
                <span></span>
              
              </label>
              <label>
                <button
                  type="button"
                  onClick={() => deleteIcon("", values.menu)}
                >
                  
                </button>
              </label>
              <label>
                <Field
                  type="radio"
                  name="menu"
                  value="right"
                  onClick={fieldHandler(values.menu)}
                />
                <span></span>
              </label>
            </div>
          </Form>
        )}
      </Formik>
    </>
  );
};

export default EditorIconsMenu;
