import React from "react";
import classes from "./editor.module.sass";
import frame from './../../assets/images/frame.png'
import EditorTitleContainer from "./EditorTitle/EditorTitleContainer";
import EditorMenuContainer from "./EditorMenu/EditorMenuContainer";
import EditorIconsComponentContainer from "./EditorComponents/EditorIconsComponent/EditorIconsComponentContainer";
import EditorTitleComponentConainer from "./EditorComponents/EditorTitleComponent/EditorTitleComponentContainer";
import EditorBacklightComponentContainer from "./EditorComponents/EditorBacklightComponent/EditorBacklightComponentContainer"
import PriceCalculator from "./PriceCalculator/PriceCalculator";

const Editor = (props) => {
  return (
    <div className={classes.back}>
      <div className={classes.wrapper}>
        <div className={classes.content_wrapper}>
          <img src={frame} alt="frame" />
          <EditorTitleContainer {...props} />
        </div>
        <EditorMenuContainer />
        <div className={classes.menu_wrapper}>
          {props.isTitleEnable && <EditorTitleComponentConainer />}
          {props.isSignEnable && <EditorIconsComponentContainer />}
          {props.isBacklightEnabled && <EditorBacklightComponentContainer/>}
        </div>
      </div>
      <div className={classes.calculator_wrapper}>
        <PriceCalculator/>
      </div>
    </div>
  );
};

export default Editor;
