import EditorIconsComponent from "./EditorIconsComponent";
import { connect } from "react-redux";
import { setIcons, setActiveIcon } from "./../../../../redux/editor-reducer";

const EditorIconsComponentContainer = (props) => {
    return (
        <EditorIconsComponent {...props}/>
    );
};

const mapStateToProps = (state) => ({
  icons: state.editor.icons,
  activeIcons: state.editor.activeIconsMenu
})

export default connect(mapStateToProps, { setIcons, setActiveIcon })(EditorIconsComponentContainer) 