import { editorApi } from "./../api/api";

const SET_INSCRIPTION = "SET-INSCRIPTION";
const SET_VALUE = "SET-VALUE";
const SET_FONT = "SET-FONT";
const SET_TITLE_COLOR = "SET-TITLE-COLOR";
const SET_ICONS_SUCCESS = "SET-ICONS";
const SET_ACTIVE_ICON = "SET-ACTIVE-ICON";
const SET_ACTIVE_ICON_SIDE = "SET-ACTIVE-ICON-SIDE";
const SET_BACKLIGHT_STATE = "SET-BACKLIGHT-STATE";

let initialState = {
  inscription: "",
  menu: {
    backlight: false,
    title: false,
    sign: false,
  },
  font: {
    name: "",
    src: "",
    weight: "",
    style: "",
  },
  titleColor: "#76e9f1",
  icons: {
    instagram:
      "https://upload.wikimedia.org/wikipedia/commons/9/95/Instagram_logo_2022.svg",
    telegram:
      "https://upload.wikimedia.org/wikipedia/commons/8/82/Telegram_logo.svg",
    thunderbolt:
      "https://upload.wikimedia.org/wikipedia/en/0/06/Thunderbolt_Symbol.svg",
    bmw: "https://upload.wikimedia.org/wikipedia/commons/4/44/BMW.svg",
    mercedes:
      "https://upload.wikimedia.org/wikipedia/commons/9/90/Mercedes-Logo.svg",
  },
  activeIcons: {
    activeSide: "",
    activeIconLeft: "",
    activeIconRight: "",
  },
  fonts: [
    {
      name: "",
      src: "Roboto",
      weight: 400,
      style: "normal",
    },
    {
      name: " ",
      src: "Roboto",
      weight: 400,
      style: "italic",
    },
    {
      name: "",
      src: "Roboto",
      weight: 700,
      style: "normal",
    },
    {
      name: " ",
      src: "Roboto",
      weight: 700,
      style: "italic",
    },
  ],
  activeFont: "",
  isBacklightEnable: "",
};

export const editorReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_INSCRIPTION:
      return {
        ...state,
        inscription: action.inscription,
      };
    case SET_VALUE:
      let menuCopy = structuredClone(state.menu);
      menuCopy[action.field] = action.value;
      return {
        ...state,
        menu: menuCopy,
      };
    case SET_FONT:
      let fontCopy = structuredClone(state.font);
      let { name, src, weight, style } = action.font;
      fontCopy.name = name;
      fontCopy.src = src;
      fontCopy.weight = weight;
      fontCopy.style = style;
      return {
        ...state,
        font: fontCopy,
      };
    case SET_TITLE_COLOR:
      return {
        ...state,
        titleColor: action.color,
      };
    case SET_ICONS_SUCCESS:
      return {
        ...state,
        icons: action.icons,
      };
    case SET_ACTIVE_ICON:
      let activeIconsCopy = structuredClone(state.activeIcons);
      activeIconsCopy[
        action.side === "left" ? "activeIconLeft" : "activeIconRight"
      ] = action.activeIcon;
      return {
        ...state,
        activeIcons: activeIconsCopy,
      };
    case SET_ACTIVE_ICON_SIDE:
      let activeIconsSide = structuredClone(state.activeIcons);
      activeIconsSide.activeSide = action.value;
      return {
        ...state,
        activeIconsMenu: activeIconsSide,
      };
    case SET_BACKLIGHT_STATE:
      return {
        ...state,
        isBacklightEnable: action.isEnable,
      };
    default:
      return state;
  }
};

export const setInscription = (inscription) => ({
  type: SET_INSCRIPTION,
  inscription,
});
export const setValue = (field, value) => ({ type: SET_VALUE, field, value });
export const setFont = (font) => ({ type: SET_FONT, font });
export const setTitleColor = (color) => ({ type: SET_TITLE_COLOR, color });
const setIconsSucsess = (icons) => ({ type: SET_ICONS_SUCCESS, icons });
export const setActiveIcon = (activeIcon, side) => ({
  type: SET_ACTIVE_ICON,
  activeIcon,
  side,
});
export const setActiveIconSide = (value) => ({
  type: SET_ACTIVE_ICON_SIDE,
  value,
});
export const setBacklightState = (isEnable) => ({
  type: SET_BACKLIGHT_STATE,
  isEnable,
});

export const setIcons = () => {
  return (dispatch) => {
    editorApi.getIcons().then((icons) => {
      dispatch(setIconsSucsess(icons.data));
    });
  };
};
