import EditorBacklightComponent from './EditorBacklightComponent'
import { setBacklightState } from '../../../../redux/editor-reducer'
import { connect } from 'react-redux'

const EditorBacklightComponentContainer = (props) => {
    return <>
        <EditorBacklightComponent {...props}/>
    </>
}

const mapStateToProps = (state) => ({
    isBacklightEnable: state.editor.isBacklightEnable
})

export default connect(mapStateToProps, { setBacklightState })(EditorBacklightComponentContainer)