import classes from "./toggleSwitch.module.sass";
import React from "react";

const ToggleSwitch = ({isActive, setIsActive, ...props}) => {
  return (
    <>
      <div
        onClick={() => setIsActive(!isActive)}
        className={`${classes.range} ${isActive && classes.active_range}`}
      >
        <span
          className={`${classes.thumb} ${isActive && classes.active_thumb}`}
        ></span>
      </div>
    </>
  );
};

export default ToggleSwitch;
