import React, { useState, useEffect } from "react";
import classes from "./editorTitle.module.sass";
import EditorIcon from "./EditorIcon/EditorIcon";
import Input from "./../Common/ResponsiveInput/Input";
import logo from "./../../../assets/images/logo.svg"

const EditorTitle = (props) => {
  const [editMode, setEditMode] = useState(false);
  const [title, setTitle] = useState();

  useEffect(() => {
    setTitle(props.title);
    setBacklightColor(props.titleColor);
    if (!props.font.name) {
      props.setFont(props.initialFont);
    }
  }, [props.title, props.titleColor]);

  const enableEditMode = () => {
    setEditMode(true);
  };

  const disableEditMode = () => {
    setEditMode(false);
    props.setInscription(title);
  };

  const setBacklightColor = (color) => {
    document.documentElement.style.setProperty("--backlight-color", color);
  };

  return (
    <div className={classes.title_wrapper}>
      <div className={classes.icon_wrapper}>
        <EditorIcon icon={props.activeIcons.activeIconLeft} />
      </div>
      <div
        className={classes.title}
        style={{
          color: props.titleColor,
          fontFamily: props.font.src,
          fontWeight: props.font.weight,
          fontStyle: props.font.style,
        }}
      >
        {editMode ? (
          <Input disableEditMode={disableEditMode} title={title} setTitle={setTitle} editMode={editMode} />
        ) : (
          <span
            className={`${props.isBacklightEnable && classes.backlight}`}
            onDoubleClick={enableEditMode}
          >
            {props.title || " "}
          </span>
        )}
      </div>
      <div className={classes.icon_wrapper}>
        <EditorIcon icon={props.activeIcons.activeIconRight} />
      </div>
      <div className={classes.logo}>
          <img src={logo} alt="logo" />
      </div>
    </div>
  );
};

export default EditorTitle;
