import React from "react";
import { Formik, Field, Form } from "formik";
import classes from "./editorMenu.module.sass";
import onFieldChange from "../Common/FieldChangeHandler/FieldChangeHandler";

const EditorMenu = (props) => {
  const fieldHandler = onFieldChange(props.setValue);

  return <>
      <Formik
        initialValues={{
          menu: "title",
        }}
      >
        {({ values }) => (
          <Form>
            <div
              className={classes.wrapper}
              role="group"
              aria-labelledby="radio-group"
            >
              <label>
                <Field
                  type="radio"
                  name="menu"
                  value="title"
                  onClick={fieldHandler("title", values.menu)}
                />
                <span></span>
              </label>
              <label>
                <Field
                  type="radio"
                  name="menu"
                  value="sign"
                  onClick={fieldHandler("sign", values.menu)}
                />
                <span></span>
              </label>
              <label>
                <Field
                  type="radio"
                  name="menu"
                  value="backlight"
                  onClick={fieldHandler("backlight", values.menu)}
                />
                <span></span>
              </label>
            </div>
          </Form>
        )}
      </Formik>
    </>
};

export default EditorMenu;
