import React from "react";
import { connect } from 'react-redux'
import { setFont } from "../../../../redux/editor-reducer"
import EditorTitleComponent from "./EditorTitleComponent";

const EditorTitleComponentContainer = (props) => {
  return (
    <EditorTitleComponent {...props}/>
  );
};

const mapStateToProps = (state) => ({
  font: state.editor.font,
  fonts: state.editor.fonts
})

export default connect(mapStateToProps, { setFont })(EditorTitleComponentContainer);
