import React from "react";
import classes from "./header.module.sass";
import { NavLink } from "react-router-dom";
import logo from "./../../assets/images/logo.svg";

const Header = () => {

  const selectLink = () => {
    return (
      NavLink => NavLink.isActive ? classes.active : ""
    )
  }

  return (
    <header className={classes.header}>
      <div className={classes.wrapper}>
        <div className={classes.logo_wrapper}>
          <img className={classes.logo} src={logo} alt="logo" />
        </div>
        <ul className={classes.links_wrapper}>
          <li className={classes.link}>
            <NavLink className={selectLink()} to="editor">
              
            </NavLink>
          </li>
          <li className={classes.link}>
            <NavLink className={selectLink()} to="progect">
              VIP-
            </NavLink>
          </li>
          <li className={classes.link}>
            <NavLink className={selectLink()} to="about">
               
            </NavLink>
          </li>
          <li className={classes.link}>
            <NavLink className={selectLink()} to="delivery">
              
            </NavLink>
          </li>
        </ul>
        <div className={classes.link}>
          <NavLink to="basket">
            
          </NavLink>
        </div>
      </div>
    </header>
  );
};

export default Header;
