import React from "react";
import classes from "./editorIcons.module.sass";
import { HandySvg } from "handy-svg";
import EditorIconsMenuContainer from "./EditorIconsMenu/EditorIconsMenuContainer";

const EditorIconsComponent = (props) => {
  let iconItems = [];
  for (let link in props.icons) {
    iconItems.push(
      <HandySvg
        onClick={() => {
          props.setActiveIcon(props.icons[link], props.activeIcons.activeSide);
        }}
        className={classes.icon}
        src={props.icons[link]}
      />
    );
  }

  return (
    <div className={classes.wrapper}>
      <EditorIconsMenuContainer />
      <div className={classes.icons_wrapper}>
        {iconItems}
      </div>
    </div>
  );
};

export default EditorIconsComponent;
