import React from "react";
import classes from "./editorTitleComponent.module.sass";
import { Formik, Field, Form } from "formik";
import ColorPickerContainer from "../../Common/ColorPicker/ColorPickerContainer";
import GoogleFontLoader from "react-google-font-loader";

const EditorTitleComponent = (props) => {
  const fieldHandler = props.setFont;

  <GoogleFontLoader
    font={[
      {
        font: props.font.src,
        weight: props.font.weight,
      },
    ]}
  />

  const fonts = props.fonts.map((font) => (
    <label>
      <Field
        type="radio"
        name="font"
        value={font.name}
        onClick={() => fieldHandler(font)}
      />
      <span>{font.name}</span>
    </label>
  ));

  return (
    <section className={classes.container}>
      <Formik
        enableReinitialize={true}
        initialValues={{
          font: props.font.name || "",
        }}
      >
        {({ values }) => (
          <Form>
            <div
              className={classes.wrapper}
              role="group"
              aria-labelledby="radio-group"
            >
              {fonts}
            </div>
          </Form>
        )}
      </Formik>
      <div className={classes.colorPicker_container}>
        <ColorPickerContainer />
      </div>
    </section>
  );
};

export default EditorTitleComponent;
